import React from "react";
import s from './Badge.module.css';
import Preloader from "../../common/Preloader/Preloader";
import baseAva from '../../../Icons/Profile.svg'
import ProfileStatus from "./ProfileStatus/ProfileStatus";

const Badge =React.memo((props) => {
    window.scrollTo(0, 0)

    const onAvaSelected = (e)=>{
        e.target.files.length && props.uploadAvatar(e.target.files[0]);
    }

    if (!props.isFetching && props.profile) {

        return <section className={s.badge}>
            <div><img src={props.profile.photos.large || baseAva} className={s.ava}/>
            {props.isOwner && <div><input type={"file"} onChange={onAvaSelected}/></div>}</div>

            <div className={s.personal}>
                <h1 className={s.name}>{props.profile.fullName}</h1>
                <ProfileStatus isOwner={props.isOwner} status={props.status} updateStatus={props.updateStatus} />
                <div className={s.infoContainer}>
                    <ul className={s.list}>
                        {Object.keys(props.profile.contacts).filter(key=>{return props.profile.contacts[key] && key }).map(key=>{
                            return <ContactTitles contactTitle={key}  key={key} />
                        })}
                        <li className={s.itemPoint}>Job looking status:</li>
                    </ul>
                    <ul className={s.list}>
                        {/*{props.profile.lookingForAJob?  <p>{props.profile.lookingForAJobDescription}</p>: null}*/}
                        {Object.keys(props.profile.contacts).filter(key=>{return props.profile.contacts[key] && key }).map(key=>{
                            return <ContactValues contactValue={props.profile.contacts[key]}  key={key} />
                        })}

                    </ul>
                </div>
                <div>
                </div>
            </div>
        </section>
    } else {
        return <div className={s.preloaderContainer}><Preloader height={90}/></div>
    }

})

const ContactTitles = ({contactTitle})=>{
    return <li className={s.itemPoint}>{contactTitle}</li>
}
const ContactValues = ({contactValue})=>{
    return <li className={s.itemPoint}><a className={s.links} href={contactValue} target={"_blank"}>click</a> </li>
}

export default Badge;